.home{
    position: relative;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    align-items: center;
}
.intro{
    max-width: 540px;
    background-color: rgb(189, 170, 146);
    box-shadow: 0px 20px  20px 1px rgba(20, 40, 20, 10%);
}
.home__img{
    border: 1px solid transparent;
    border-top-right-radius: 20%; 
}
.home__name{
    font-size: var(--h1-font-size);
    font-weight: var(--font-bold);
    margin-bottom: 1.25rem;
}
.home__profession{
    font-size: var(--larg-font-size);
    color: var(--text-color-light);
    margin-bottom: 2.5rem;
}
.home__socials{
    display: flex;
    column-gap: 1.5rem;
    margin-bottom: 2.5rem;
    align-items: center;
    justify-content: center;
    margin-top: 20px;
}
.home__socials a{
    color: var(--text-color-light);
    font-size: 1.5rem;
    transition: 0.3s;
}
.home__socials-links:hover {
    font-size: 2.5rem;
    transition: 0.3s;
}