@import url('https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,300..900;1,300..900&display=swap');


/*============Google Fonts============*/




/*============Variables Css============*/

:root {
    --first-color: hsl(353, 88%, 84%);
    --title-color: hsl(0, 0%, 0%);
    --text-color: hsl(0, 0%, 0%);
    --footer-text-color: hsl(0, 0%, 100%);
    --body-color: #f2fafc;
    --container-color: hsl(253, 100%, 65%);
    --background-color: hsl(0, 2%, 57%);
    --body-font-color: hsl(0, 0%, 0%);
    --footer-bg-color: hsl(0, 0%, 13%);

  /*============Font and typography============*/
  /* .5rem = 8px | 1rem = 16px...*/
  
  --body-font: 'Rubik', sans-serif;
  --h1-font-size: 2.25rem;
  --h2-font-size: 1.5rem;
  --h3-font-size: 1.25rem;
  --normal-font-size: 1rem;
  --small-font-size: .875rem;
  --smaller-font-size: .813rem;

  /*============Font weight============*/
  --font-medium: 500;
  --font-semi-bold: 600;
  --font-bold: 700;

  /*============Box Shadow============*/

  --shadow: 0px 5px 20px 0px rgb(69 67 96 / 10%);

}

  /*============Responsive typography============*/

@media screen and (max-width: 1024px){
    :root{
        --h1-font-size: 1.75rem;
        --h2-font-size: 1.25rem;
        --h3-font-size: 1rem;
        --normal-font-size: 0.938rem;
        --small-font-size: 0.813rem;
        --smaller-font-size: 0.75rem;
    }
  
}

  /*============Base Css============*/

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html{
    font-size: 62.5%;
}

body{
    font-family: var(--body-font);
    line-height: 1.7;
    background-color: var(--body-color);
    color: var(--text-color);
    font-size: var(--h1-font-size); 
}

h1, h2, h3, h4, h5, h6{
    color: var(--title-color);
    font-weight: var(--font-bold);
    line-height: 1.2;
}

ul{
    list-style: none;
}

a{
    text-decoration: none;
} 

button{
    outline: none;
    border: none;
    cursor: pointer;
}

img{
    max-width: 100%;
    height: auto;
    vertical-align: middle;
  } 

/*============Container============*/

.container{
    max-width: 100%;
    text-align: center;
    padding: 50px;
}

.grid{
    display: grid;
    gap: 1.5rem;
}

.section{
  padding: 4rem 0;
  padding-bottom: 2rem;
}

.section__title{
  font-size: var(--h1-font-size);
  font-weight:  var(--font-bold);
  margin-bottom: 3.75rem;
}

.btn{
  cursor: pointer;
  padding: 1px;
  border-radius: 5px;
  box-shadow: var(--shadow);
  border: 1px solid transparent;
  color: #ffffff;
  transition: all 0.3s ease;
  font-weight: var(--font-bold);
}
.btn:hover{
  background-color: var(--first-color);
  color: var(--text-color);
}



