.slider__slick{
    justify-content: center;
    align-items: center;
    display: flex;
    box-shadow: var(--shadow);
}

.slick-slider {
    width: 50%;
    margin-top: 2rem;
    margin-bottom: 10rem;
}
.slide{
    padding: 40px;
    text-align: center;
}

/* Slider Arrows */
.slick-prev,
.slick-next {
    background-color: #0a0a0a;
    /* Change the background color of the arrows */
    color: #000000;
    /* Change the color of the arrows */
    border-radius: 50%;
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1;
}

.slide_link{
    font-size: 4rem;
    color: var(--primary-color);
}