@import url(https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,300..900;1,300..900&display=swap);
/*============Google Fonts============*/




/*============Variables Css============*/

:root {
    --first-color: hsl(353, 88%, 84%);
    --title-color: hsl(0, 0%, 0%);
    --text-color: hsl(0, 0%, 0%);
    --footer-text-color: hsl(0, 0%, 100%);
    --body-color: #f2fafc;
    --container-color: hsl(253, 100%, 65%);
    --background-color: hsl(0, 2%, 57%);
    --body-font-color: hsl(0, 0%, 0%);
    --footer-bg-color: hsl(0, 0%, 13%);

  /*============Font and typography============*/
  /* .5rem = 8px | 1rem = 16px...*/
  
  --body-font: 'Rubik', sans-serif;
  --h1-font-size: 2.25rem;
  --h2-font-size: 1.5rem;
  --h3-font-size: 1.25rem;
  --normal-font-size: 1rem;
  --small-font-size: .875rem;
  --smaller-font-size: .813rem;

  /*============Font weight============*/
  --font-medium: 500;
  --font-semi-bold: 600;
  --font-bold: 700;

  /*============Box Shadow============*/

  --shadow: 0px 5px 20px 0px rgb(69 67 96 / 10%);

}

  /*============Responsive typography============*/

@media screen and (max-width: 1024px){
    :root{
        --h1-font-size: 1.75rem;
        --h2-font-size: 1.25rem;
        --h3-font-size: 1rem;
        --normal-font-size: 0.938rem;
        --small-font-size: 0.813rem;
        --smaller-font-size: 0.75rem;
    }
  
}

  /*============Base Css============*/

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html{
    font-size: 62.5%;
}

body{
    font-family: var(--body-font);
    line-height: 1.7;
    background-color: var(--body-color);
    color: var(--text-color);
    font-size: var(--h1-font-size); 
}

h1, h2, h3, h4, h5, h6{
    color: var(--title-color);
    font-weight: var(--font-bold);
    line-height: 1.2;
}

ul{
    list-style: none;
}

a{
    text-decoration: none;
} 

button{
    outline: none;
    border: none;
    cursor: pointer;
}

img{
    max-width: 100%;
    height: auto;
    vertical-align: middle;
  } 

/*============Container============*/

.container{
    max-width: 100%;
    text-align: center;
    padding: 50px;
}

.grid{
    display: grid;
    grid-gap: 1.5rem;
    gap: 1.5rem;
}

.section{
  padding: 4rem 0;
  padding-bottom: 2rem;
}

.section__title{
  font-size: var(--h1-font-size);
  font-weight:  var(--font-bold);
  margin-bottom: 3.75rem;
}

.btn{
  cursor: pointer;
  padding: 1px;
  border-radius: 5px;
  box-shadow: var(--shadow);
  border: 1px solid transparent;
  color: #ffffff;
  transition: all 0.3s ease;
  font-weight: var(--font-bold);
}
.btn:hover{
  background-color: var(--first-color);
  color: var(--text-color);
}




.main{
    margin-left: 20px;
}
.home{
    position: relative;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    grid-gap: 1.5rem;
    gap: 1.5rem;
    align-items: center;
}
.intro{
    max-width: 540px;
    background-color: rgb(189, 170, 146);
    box-shadow: 0px 20px  20px 1px rgba(20, 40, 20, 10%);
}
.home__img{
    border: 1px solid transparent;
    border-top-right-radius: 20%; 
}
.home__name{
    font-size: var(--h1-font-size);
    font-weight: var(--font-bold);
    margin-bottom: 1.25rem;
}
.home__profession{
    font-size: var(--larg-font-size);
    color: var(--text-color-light);
    margin-bottom: 2.5rem;
}
.home__socials{
    display: flex;
    grid-column-gap: 1.5rem;
    column-gap: 1.5rem;
    margin-bottom: 2.5rem;
    align-items: center;
    justify-content: center;
    margin-top: 20px;
}
.home__socials a{
    color: var(--text-color-light);
    font-size: 1.5rem;
    transition: 0.3s;
}
.home__socials-links:hover {
    font-size: 2.5rem;
    transition: 0.3s;
}
.aside{
    position: fixed;
    width: 10px;
    top: 0;
    left: 0;
    background-color: var(--background-color);
    border-right: 1px solid rgba(0, 0, 0, 0.5);
    border: transparent;
    padding: 2.5rem;
    box-shadow: var(--shadow);
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    z-index: 10;
}

.nav__list{
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-row-gap: 1rem;
    row-gap: 1rem;
}
.nav__link{
    line-height:2rem;
    color: var(--font-color);
    font-weight: var(--font-bold); 
}
.nav__link:hover{
    position: relative;
    font-size: 4rem;
    transition: all 0.3s;
    color: var(--first-color);
    font-weight: var(--font-bold);
}


.copyRight{
    color: hsl(0, 0%, 100%);
    font-size: var(--small-font-size);
    transform: rotate(-90deg);
    writing-mode: vertical-rl;
}
.about__text {
  font-size: 2.5rem;
  text-align: center;
}

.slider__slick{
    justify-content: center;
    align-items: center;
    display: flex;
    box-shadow: var(--shadow);
}

.slick-slider {
    width: 50%;
    margin-top: 2rem;
    margin-bottom: 10rem;
}
.slide{
    padding: 40px;
    text-align: center;
}

/* Slider Arrows */
.slick-prev,
.slick-next {
    background-color: #0a0a0a;
    /* Change the background color of the arrows */
    color: #000000;
    /* Change the color of the arrows */
    border-radius: 50%;
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1;
}

.slide_link{
    font-size: 4rem;
    color: var(--primary-color);
}

.master-education-card{
    margin-top: 40px;
}







#contact__container{
    display: grid;
    grid-gap: 1.5rem;
    gap: 1.5rem;
    margin-top: 5rem;
    background-color: var(--footer-bg-color);
    
}
#btn_contact{
    background-color: var(--footer-text-color);
}
